import { css } from 'lit';

export const styles = css`:host {
  --elevation: var(--ig-dialog-elevation, var(--ig-elevation-0));
  --background: var(--ig-dialog-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --title-color: var(--ig-dialog-title-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --message-color: var(--ig-dialog-message-color, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --border-color: var(--ig-dialog-border-color, hsla(var(--ig-gray-300), var(--ig-gray-a)));
  --backdrop: var(--ig-dialog-backdrop, hsla(var(--ig-gray-500), 0.54));
  --border-radius: var(--ig-dialog-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.1111111111) * 2.25rem), 2.25rem));
  background: var(--background);
  border-radius: var(--border-radius);
}

[part~=backdrop] {
  background: var(--backdrop);
}

[part=base] {
  min-width: 18rem;
  border: 1px solid var(--border-color);
  -webkit-box-shadow: var(--elevation);
          box-shadow: var(--elevation);
}

[part=title],
[part=footer] {
  font-family: var(--ig-h5-font-family, var(--ig-font-family));
  font-size: var(--ig-h5-font-size);
  font-weight: var(--ig-h5-font-weight);
  font-style: var(--ig-h5-font-style);
  line-height: var(--ig-h5-line-height);
  letter-spacing: var(--ig-h5-letter-spacing);
  text-transform: var(--ig-h5-text-transform);
  margin-top: var(--ig-h5-margin-top);
  margin-bottom: var(--ig-h5-margin-bottom);
  margin: 0;
  color: var(--title-color);
  padding: 1rem;
}

[part=content] {
  color: var(--message-color);
  padding: 1rem;
}

[part=title] {
  -webkit-border-after: 1px solid var(--border-color);
          border-block-end: 1px solid var(--border-color);
}

[part=footer] {
  -webkit-border-before: 1px solid var(--border-color);
          border-block-start: 1px solid var(--border-color);
}`;
